@media (min-width: 768px) {
  .row-md-eq {
    display: flex;
  }
}
.col-middle {
  flex-direction: column;
  vertical-align: middle;
  align-self: center;
}
.col-flex {
  display: flex;
  flex-direction: column;
}
.col-flex .align-bottom {
  margin-top: auto;
}
.navbar {
  border: 0;
}
.navbar-right {
  margin-right: 0;
}
.navbar-brand {
  padding-bottom: 10px;
  padding-top: 10px;
}
.navbar-brand > img {
  max-height: 60px;
}
.navbar-toggle .icon-bar {
  transition: all 0.2s ease-in-out;
}
.navbar-toggle .bar-top {
  transform-origin: 10% 10%;
  transform: rotate(45deg);
}
.navbar-toggle .bar-middle {
  opacity: 0;
}
.navbar-toggle .bar-bottom {
  transform-origin: 10% 90%;
  transform: rotate(-45deg);
}
.navbar-toggle.collapsed .bar-top,
.navbar-toggle.collapsed .bar-bottom {
  transform: rotate(0);
}
.navbar-toggle.collapsed .bar-middle {
  opacity: 1;
}
.img-responsive {
  width: 100%;
}
.section {
  margin-bottom: 60px;
}
.section-intro {
  background: #5e6d7b url('../img/hero-bg.jpg') center center no-repeat;
  background-size: cover;
  color: #fff;
  padding-bottom: 60px;
}
.section-intro .navbar {
  margin-bottom: 60px;
}
.section-intro .section-header {
  font-size: 24px;
  margin-bottom: 60px;
}
@media (min-width: 768px) {
  .section-intro .section-header {
    font-size: 36px;
    margin-bottom: 0;
  }
}
.label {
  border-radius: 0;
  font-weight: 300;
  text-transform: uppercase;
}
.read-more {
  display: inline-block;
  margin-top: 10px;
  padding-bottom: 20px;
  padding-left: 40px;
  position: relative;
}
.read-more:before {
  border: 20px solid #f41215;
  border-right-color: transparent;
  border-top-color: transparent;
  bottom: 0;
  content: "";
  display: block;
  left: 0;
  position: absolute;
}
.read-more:after {
  bottom: 4px;
  color: #fff;
  content: "\2192";
  font-weight: 200;
  left: 8px;
  line-height: 1;
  position: absolute;
}
.entry {
  margin-bottom: 60px;
}
.entry-title:first-child {
  margin-top: 0;
}
.entry-title > small {
  display: block;
  margin-bottom: 1em;
}
.panel {
  border-width: 8px;
  box-shadow: none;
}
.panel-activity .panel-body {
  padding-bottom: 60px;
}
@media (min-width: 480px) and (max-width: 767px) {
  .panel-activity .panel-body {
    padding-bottom: 90px;
  }
}
@media (min-width: 768px) {
  .panel-activity .panel-body {
    padding: 60px 60px 140px;
  }
}
.activity-mosaic {
  display: block;
  margin-bottom: -60px;
  margin-left: auto;
  margin-right: auto;
  max-width: 80%;
  position: relative;
  top: -60px;
}
@media (min-width: 480px) and (max-width: 767px) {
  .activity-mosaic {
    margin-bottom: -90px;
    top: -90px;
  }
}
@media (min-width: 768px) {
  .activity-mosaic {
    margin-bottom: -150px;
    top: -150px;
  }
}
.divider {
  border-top-width: 2px;
  margin-left: 0;
  margin-right: 0;
  max-width: 180px;
}
.divider-center {
  margin-left: auto;
  margin-right: auto;
}
.divider-danger {
  border-color: #f41215;
}
.list {
  list-style: none;
  padding-left: 0;
}
.list li {
  padding-left: 1.5em;
  position: relative;
}
.list li:before {
  content: "";
  display: inline-block;
  left: 0;
  position: absolute;
  top: 0;
}
.list-checkbox {
  color: #999;
  padding-left: 0;
}
.list-checkbox li {
  margin-bottom: 20px;
  padding-left: 2em;
}
.list-checkbox li:before {
  background-image: url('../img/sprite-checkbox.png');
  background-repeat: none;
  background-size: 1em auto;
  height: 1em;
  top: 2px;
  width: 1em;
}
.list-checkbox li.checked {
  color: #00a0e8;
}
.list-checkbox li.checked:before {
  background-position: left bottom;
}
.list-bulleted li:before {
  color: #f41215;
  content: "\25cf";
}
.list-relaxed li {
  margin-bottom: 10px;
}
.footer {
  color: #00a0e8;
  text-align: center;
}
.footer .section-title {
  font-size: 80px;
}
.footer strong {
  color: #6396ad;
}
.fb-page {
  display: block !important;
}
/*# sourceMappingURL=style.css.map */